<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full  d-flex">
                        <h4 class="modal-title">{{ $t('BatchProcessDetail') }}</h4>
                        <button type="button" class="close" v-on:click="Close">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>

                    </div>
                    <div class="modal-body">
                            <tg-list :IsShowAction="false" ModuleName="Billing" SubModuleCode="BatchProcess" :LegendArray="[]"
                            :RenderRowActionMethod="() => { }" v-on:ActionButtonClick="actionButtonClick" :widgets="[]"
                            IdentityColumn="" :listType="['List']" :ListData="batchProcessData" :HeaderData="Headers"
                            :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecords" :ListDataCallBackFunction="FetchData"
                            :showCheckBox="true" :SortExp="SortExp" :SortBy="SortBy"
                            :NorecordfoundText="$t('NoRecordfound')" v-on:LeftsearchButtonClick="leftCommonSearch"
                            v-on:PagerButtonClick="pagerMethod" v-on:HeaderButtonClick="commonHeaderButtonClick"
                            v-on:SortdataButtonClick="sortdata">
                        </tg-list>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import DataServices from '../../services/DataService'
export default {
    props: {
        BatchId: {
            type: Object,
            required: true
        }
    },
    data() {
        return {
            isLoading: false,
            HeaderText: '',
            TagId:0,
            TagArr:[],
            AddTagPopup: false,
            listheaderbuttons: [],
             Headers: [
                {
                    COLUMN_NAME: "Client_Name",
                    DATA_TYPE: "text",
                    SORTABLE: false,
                    VISIBLE: true,
                    DISPLAY_NAME: this.$t('ClientName'),
                    DISPLAY_ORDER: 1,
                },
                {
                    COLUMN_NAME: "serial_number",
                    DATA_TYPE: "text",
                    SORTABLE: false,
                    VISIBLE: true,
                    DISPLAY_NAME: this.$t('SerialNumber'),
                    DISPLAY_ORDER: 2,
                },
                {
                    COLUMN_NAME: "Consumption",
                    DATA_TYPE: "text",
                    SORTABLE: false,
                    VISIBLE: true,
                    DISPLAY_NAME: this.$t('Consumption'),
                    DISPLAY_ORDER: 3,
                },
                {
                    COLUMN_NAME: "Billing_days",
                    DATA_TYPE: "text",
                    SORTABLE: false,
                    VISIBLE: true,
                    DISPLAY_NAME: this.$t('BillingDays'),
                    DISPLAY_ORDER: 4,
                }],
            SortBy: '',
            skillStringArr: [],
            SortExp: '',
            TotalRecords: 0,
            searchCondition: '',
            PageNumber: 1,
            PageSize: 10,
            TagData: [],
            CurrentPage: 1,
            TotalPages: 0,
            skillString: '',
            leftSearchFields: [],
            noRecords:false,
            batchProcessData:[]
        }
    },
    created: function () {
        var vm = this;
        vm.FetchData();
    },
    methods: {
         actionButtonClick: function (callbackfunction, item, index, event) {

        },
        leftCommonSearch: function (value) {
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                this.searchCondition = value.searchConditionJson;
            }
            else {
                this.searchCondition = "";
            }
            this.FetchData();
        },
        sortdata: function (sortBy) {
            this.SortBy = sortBy;
            var sortExp = 'ASC';
            if (this.SortExp == 'ASC') {
                sortExp = 'DESC';
            }
            this.SortExp = sortExp;
            this.SortBy = sortBy;
            this.SortExp = sortExp;
            this.PageNumber = 1;
            this.FetchData();
        },
         commonHeaderButtonClick: function (event) {
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },
        FetchData: async function () {
            var vueObj = this;
            var params = `batchId=${vueObj.BatchId}&pageSize=${vueObj.PageSize}&pageNum=${vueObj.PageNumber}&sortBy=${vueObj.SortBy}&SortExp=${vueObj.SortExp}`;
            await DataServices.GetBatchProcessDataByBatchId(params).then((response) => {
                vueObj.batchProcessData=[];
                if (response.data && response.data.length > 0) {
                    vueObj.batchProcessData = response.data;
                    if (vueObj.batchProcessData.length > 0) {
                        vueObj.TotalRecords = response.data[0].Total_Record_Count;
                        vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.PageSize);
                        vueObj.CurrentPage = vueObj.PageNumber;
                        //vueObj.listheaderbuttons[1].isdisabled = true;
                        vueObj.noRecords = false;
                    }
                    else {
                        vueObj.noRecords = true;
                        vueObj.batchProcessData=[];
                    }
                }
                else {
                    vueObj.noRecords = true;
                    vueObj.TotalRecords = 0;
                    vueObj.batchProcessData=[];
                }
            })
        },
        Close: function () { 
            this.$parent.isOpenProcessData = false;
        }
    }
}
</script>
    
    
    