<template>
    <section>
        <tg-list :IsShowAction="true" :showCheckBox="false" ModuleName="Billing" :listType="['List']"
            :RenderRowActionMethod="renderActions" SubModuleCode="Billing" IdentityColumn="id"
            :HeaderText="$t('BatchProcessing')" :ListData="BatchProcessingListing" :HeaderData="Headers"
            :PageSize="pageSize" :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecords"
            :SearchFields="leftSearchFields" :ListDataCallBackFunction="FetchData" :LegendArray="[]" :SortExp="sortExp"
            :SortBy="sortBy" :NorecordfoundText="$t('NoRecordfound')" v-on:LeftsearchButtonClick="leftCommonSearch"
            v-on:ActionButtonClick="actionButtonClick" v-on:PagerButtonClick="pagerMethod"
            v-on:HeaderButtonClick="commonHeaderButtonClick" v-on:SortdataButtonClick="sortdata">
            <template v-slot:slotdata="{ data }">
                <template v-if="data.column.COLUMN_NAME == 'created_at'">
                    <span>
                        {{ $d(data.row.created_at) }}
                    </span>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'batch_name'">
                     <a href="javascript:;" v-on:click="OpenBatchDetail(data)">
                        <span>{{ data.row.batch_name }}</span>
                    </a>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'status'">
                    <span>
                        <!-- {{ data.row.progressValue }}
                        {{ data.row.batch_id }} -->
                        <div class="progress mb-1">
                            <div id="progressBar-30" class="progress-bar progress-bar-striped bg-warning"
                                :style="{ width: 100 + '%' }"
                                v-if="data.row.status == 'Starting' && data.row.progressValue == 0">
                                <span>{{ $t('Starting') }}</span>
                            </div>
                            <div id="progressBar-30" class="progress-bar progress-bar-striped bg-success"
                                :style="{ width: 100 + '%' }"
                                v-if="data.row.status == 'Completed' && data.row.progressValue == 0">
                                <span>{{ $t('Completed') }}</span>
                            </div>
                            <div id="progressBar-30" class="progress-bar progress-bar-striped bg-primary"
                                :style="{ width: 100 + '%' }"
                                v-else-if="data.row.status == 'Generated' && data.row.progressValue == 0">
                                <span>{{ $t('created') }}</span>
                            </div>
                            <div id="progressBar-30" class="progress-bar progress-bar-striped bg-danger"
                                :style="{ width: 100 + '%' }"
                                v-else-if="data.row.status == 'Failed' && data.row.progressValue == 0">
                                <span>{{ $t('Failure') }}</span>
                            </div>
                            <div id="progressBar-30" class="progress-bar progress-bar-striped bg-warning"
                                :style="{ width: 100 + '%' }"
                                v-else-if="data.row.status == 'Started' && data.row.progressValue <= 0">
                                <span>{{ $t('Inprogress') }}</span>
                            </div>

                            <div class="progress-bar progress-bar-striped bg-success"
                                :style="{ width: data.row.progressValue + '%' }" v-if="data.row.progressValue > 0">
                                <span v-if="data.row.progressValue === 100">{{ $t('Completed') }}</span>
                                <span>{{ $t('Failure') }}</span>
                            </div>
                            <div id="progressBar-30" class="progress-bar progress-bar-striped bg-warning"
                                :style="{ width: 100 + '%' }"
                                v-else-if="data.row.status == 'Started' && data.row.progressValue <= 0">
                                <span>{{ $t('Inprogress') }}</span>
                            </div>

                            <div class="progress-bar progress-bar-striped bg-success"
                                :style="{ width: data.row.progressValue + '%' }" v-if="data.row.progressValue > 0">
                                <span v-if="data.row.progressValue === 100">{{ $t('Completed') }}</span>
                                <span v-else>{{ data.row.progressValue }}%</span>
                            </div>
                             <div id="progressBar-0" class="progress-bar progress-bar-striped bg-danger"
                                :style="{ width: 100 + '%' }"
                                v-else-if="data.row.status == 'Pending' && data.row.progressValue == 0">
                                <span>{{ $t('Pending') }}</span>
                            </div>
                        </div>
                    </span>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'stats'">
                    <span v-if="data.row.status == 'Generated'">
                        --
                    </span>
                    <span v-else>
                        {{ data.row.stats }}
                    </span>
                </template>
                 <template v-if="data.column.COLUMN_NAME == 'record_count'">
                    <span>
                        <a id="ancProjectDetail" :class="{ 'text-dark text-center': true, 'disabled': (data.row.record_count == 0) }"
                        href="javascript:;" v-on:click="OpenBatchProcessDataLising(data.row.batch_id)">
                        <i class="fa fa-file-o" aria-hidden="true"></i>
                        <span class="noti-circle noti-orange">{{ data.row.record_count }}</span>
                        </a>
                    </span>
                </template>
            </template>
        </tg-list>
        <CreateBatch v-if="isCreateBatch" :callbackfunction="FetchData" :BatchId="BatchId"></CreateBatch>
        <RecordDetails v-if="isRecordDetail"></RecordDetails>
        <Reprocess v-if="isReprocess" :reProcessDetail="reProcessDetail" :reProcessIndex="reProcessIndex"></Reprocess>
        <transition name="modal">
            <div class="my-popups">
                <loader :is-visible="isLoading"></loader>
                <div class="modal d-block dialog" v-if="isDetail">
                    <div class="modal-dialog modal-lg">
                        <div class="modal-content">
                            <div class="modal-header">
                                <h4 class="modal-title">{{ $t('BatchDetail') }}</h4>

                                <button type="button" class="close" v-on:click="CloseBatchDetail">
                                    <em class="fa fa-times" aria-hidden="true"></em>
                                </button>

                            </div>

                            <div class="modal-body">
                                <div class="col-md-12 p-0">
                                    <div class="listing">
                                        <div class="card mb-3 probx">
                                            <div class="card-header">
                                                <h2>{{ $t('Details') }}</h2>
                                                <div class="cont_right">
                                                    <div class="bar">
                                                    </div>
                                                </div>
                                            </div>
                                            <div class="card-body">
                                            <div class="row">
                                            <div class="col-md-6 col-lg-4" v-for="(field, index) in displayFields" :key="index">
                                            <div class="form-group">
                                               <label><strong>{{ field.label }}:</strong>
                                               <span class="answer rightlabeltext ml-1" :class="field.key === 'status' ? 'status-badge' : ''" style="display: inline !important;">{{ formatFieldValue(field.key) }}</span></label>
                                            </div>
                                            </div>
                                            </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>

                            <div class="modal-footer">
                                <button @click="CloseBatchDetail" class="btn btn-danger">
                                    <em class="fa fa-times pr-2" aria-hidden="true"></em> {{ $t('Cancel') }}
                                </button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </transition>
         <BatchProcessDataListing v-if="isOpenProcessData" :BatchId="BatchId" ></BatchProcessDataListing>
    </section>
</template>
<script>
import DataService from '../../services/DataService';
import CreateBatch from '../../components/Modals/CreateBatch.vue';
import Schema from '../Billing/BatchProcessingSchema';
import RecordDetails from '../../components/Modals/RecordDetails.vue';
import Reprocess from '../../components/Modals/Reprocess.vue';
import BatchProcessDataListing from '../../components/Modals/BatchProcessDataListing.vue';

export default ({
    components: { CreateBatch, RecordDetails, Reprocess, BatchProcessDataListing },
    computed: {
    displayFields() {
        return [
        { key: 'batch_name', label: this.$t('batchname') },
        { key: 'customer_type_name', label: this.$t('CustomerType') },
        { key: 'station_name', label: this.$t('StationName') },
        { key: 'plan_name', label: this.$t('PlanName') },
        { key: 'state', label: this.$t('State') },
        { key: 'district', label: this.$t('District') },
        { key: 'zip_code', label: this.$t('ZIPCODE') },
        { key: 'status', label: this.$t('Status') },
        { key: 'processed_by', label: this.$t('processedby') },
        { key: 'created_at', label: this.$t('CreatedAt') },
        { key: 'record_count', label: this.$t('RecordCount') },
        { key: 'total_records', label: this.$t('TotalRecords') },
        { key: 'progressValue', label: this.$t('ProgressValue') },
        { key: 'stats', label: this.$t('Stats') },
        ];
    }
    },

    data() {
        return {
            reProcessDetail: {},
            reProcessIndex: 0,
            isReprocess: false,
            Headers: [
                {
                    COLUMN_NAME: "batch_name",
                    DATA_TYPE: "text",
                    SORTABLE: false,
                    VISIBLE: true,
                    DISPLAY_NAME: this.$t('batchname'),
                    DISPLAY_ORDER: 1,
                    settings: {
                        isInSlot: true,
                    }
                },
                {
                    COLUMN_NAME: "customer_type_name",
                    DATA_TYPE: "text",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: this.$t('CustomerType'),
                    DISPLAY_ORDER: 2,
                },
                {
                    COLUMN_NAME: "plan_name",
                    DATA_TYPE: "text",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: this.$t('Plan'),
                    DISPLAY_ORDER: 3,
                },
                {
                    COLUMN_NAME: "station_name",
                    DATA_TYPE: "text",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: this.$t('StationName'),
                    DISPLAY_ORDER: 4,
                },
                {
                    COLUMN_NAME: "created_at",
                    DATA_TYPE: "text",
                    SORTABLE: false,
                    VISIBLE: true,
                    DISPLAY_NAME: this.$t('CreatedDate'),
                    DISPLAY_ORDER: 5,
                    settings: {
                        isInSlot: true,
                    }
                },
                {
                    COLUMN_NAME: "status",
                    DATA_TYPE: "text",
                    SORTABLE: false,
                    VISIBLE: true,
                    DISPLAY_NAME: this.$t('Status'),
                    DISPLAY_ORDER: 6,
                    settings: {
                        isInSlot: true,
                    }
                },
                {
                    COLUMN_NAME: "record_count",
                    DATA_TYPE: "text",
                    SORTABLE: false,
                    VISIBLE: true,
                    DISPLAY_NAME: this.$t('RecordCount'),
                    DISPLAY_ORDER: 7,
                    settings: {
                        isInSlot: true,
                    }
                },
                {
                    COLUMN_NAME: "stats",
                    DATA_TYPE: "text",
                    SORTABLE: false,
                    VISIBLE: true,
                    DISPLAY_NAME: this.$t('stats'),
                    DISPLAY_ORDER: 8,
                    settings: {
                        isInSlot: true,
                    }
                }
            ],
            TotalRecords: 0,
            pageSize: 10,
            pageNum: 1,
            sortBy: '',
            searchCondition: '',
            leftSearchFields: Schema.leftsearchSchema,
            listActions:
                [
                     {
                        title: 'Edit',
                        callbackfunction: 'EditRuleService',
                        href: 'javascript:void(0)',
                        icon: 'fa fa-pencil text-success',
                        additionalSpan: '',
                        isDisabled: false,
                        isvisible: true,
                    },
                    {
                        title: 'Process',
                        href: 'javascript:void(0)',
                        icon: 'fa fa-share-square-o ',
                        callbackfunction: 'Process',
                        additionalSpan: ''
                    },
                    {
                        title: 'Detail',
                        href: 'javascript:void(0)',
                        icon: 'fa fa-eye',
                        // isDisabled: true,
                        callbackfunction: 'Detail',
                        additionalSpan: ''
                    },
                    {
                        title: 'reprocess',
                        href: 'javascript:void(0)',
                        icon: 'fa fa-repeat',
                        // isDisabled: true,
                        callbackfunction: 'Reprocess',
                        additionalSpan: ''
                    }
                ],
            listheaderbuttons: [
                // {
                //     title: 'generatebatch',
                //     iconClass: 'fa fa-spinner',
                //     callbackfunction: 'GenerateBatch',
                //     additionalClass: '',
                //     isdisabled: false,
                //     isvisible: true,
                //     checkPrivilege: false
                // },
                // {
                //     title: 'QueueList',
                //     iconClass: 'fa fa-list',
                //     callbackfunction: 'queueList',
                //     additionalClass: '',
                //     isdisabled: false,
                //     isvisible: true,
                //     checkPrivilege: false
                // }
            ],
            GroupStart: 1,
            GroupEnd: 3,
            PlanId: 0,
            CurrentPage: 1,
            TotalPages: 0,
            sortExp: '',
            requestId: '',
            BatchProcessingListing: [],
            // BatchProcessingListingStart:[],
            noRecords: false,
            isCreateBatch: false,
            isRecordDetail: false,
            batchName: '',
            intervalId: [],
            BatchId: '',
            isDetail: false,
            BatchData: [],
            isOpenProcessData: false,
            batchProcessData: []
        }
    },
    created: async function () {
        var vm = this;
        await Schema.bindleftCommonSearchdropdown(this, 'Billing', 'CUSTOMER_TYPE', null);
        await Schema.bindleftCommonSearchdropdown(this, 'Billing', 'PLANS', null);
        await Schema.bindleftCommonSearchdropdown(this, 'Billing', 'STATION_LIST', null);
        vm.BatchId =vm.$route.params.id;
        await vm.FetchData();

    },
    // updated() {
    //     // Automatically start polling progress for each batch row.
    //     var vm = this;
    //     vm.startAllProgress();
    // },
    methods: {
        // Start polling for all batches as soon as the component mounts
        startAllProgress: function () {
            var vm = this;
            vm.BatchProcessingListing.forEach((batch, index) => {
                var batch_status = batch.status;
                if (batch_status == 'Started')
                    vm.getProgress(index, batch.batch_id);  // Start progress polling for each batch
            });
        },

        getProgress: function (index, batchId) {
            this.intervalId[batchId] = setInterval(async () => {  // Make the callback async
                try {
                    var url = `batchId=${batchId}`;
                    let response = await DataService.GetBatchProgress(url);
                    if (response.data.result != null) {
                        this.BatchProcessingListing.filter(x => x.batch_id == batchId)[0].status = JSON.parse(response.data.result).status;
                        // this.BatchProcessingListing[index].status = JSON.parse(response.data.result).status;
                        let progressValue = JSON.parse(response.data.result).progress_value;

                        if (progressValue < 100) {
                            this.BatchProcessingListing.filter(x => x.batch_id == batchId)[0].progressValue = progressValue;
                            // this.BatchProcessingListing[index].progressValue = progressValue;
                        }
                        else {
                            this.BatchProcessingListing.filter(x => x.batch_id == batchId)[0].progressValue = 100;
                            // this.BatchProcessingListing[index].progressValue = 100;
                            // Optionally refresh data after completion
                            // this.FetchData();
                            clearInterval(this.intervalId[batchId]);  // Stop the interval once progress reaches 100
                            delete this.intervalId[batchId];  // Remove the intervalId from the object
                        }
                        this.BatchProcessingListing.filter(x => x.batch_id == batchId)[0].stats = JSON.parse(response.data.result).stats;
                        // this.BatchProcessingListing[index].stats = JSON.parse(response.data.result).stats;
                    }
                } catch (error) {
                    console.error('Error fetching progress:', error);
                    clearInterval(this.intervalId[batchId]);  // Stop the interval in case of error
                }
            }, 15000); // Poll every 15 seconds
        },

        OpenBatchDetail: async function(item){
            var vm= this;
            vm.isDetail=true;
            vm.BatchData=item.row;
        },
        formatFieldValue(key) {
            if (!this.BatchData) return '--';

            const val = this.BatchData[key];

            // Handle different field mappings
            let actualValue = val;
            if (key === 'total_records' && this.BatchData.TOTALRECORDS !== undefined) {
            actualValue = this.BatchData.TOTALRECORDS;
            }

            if (actualValue === null || actualValue === undefined || actualValue === '') return '--';

            if (key === 'created_at') {
            const d = new Date(actualValue);
            if (!isNaN(d)) {
                const date = d.toLocaleDateString();
                const time = d.toLocaleTimeString([], { hour: '2-digit', minute: '2-digit', second: '2-digit' });
                return `${date}, ${time}`;
            } else {
                return actualValue;
            }
            }

            if (key === 'status') {
            // Capitalize first letter for status display
            return actualValue.charAt(0).toUpperCase() + actualValue.slice(1);
            }

            if (key === 'stats' && actualValue) {
            try {
                const statsObj = typeof actualValue === 'string' ? JSON.parse(actualValue) : actualValue;
                if (statsObj && typeof statsObj === 'object') {
                const success = statsObj.success || 0;
                const failure = statsObj.failure || 0;
                return `Success: ${success}, Failure: ${failure}`;
                }
            } catch (e) {
                // If parsing fails, return as is
            }
            }

            return actualValue;
        },
         OpenBatchProcessDataLising: async function(id) {
            var vm = this;
            this.BatchId = id;
            this.isOpenProcessData = !this.isOpenProcessData; 
            },
        callProcessBatch: async function (item, index, reprocess) {
            var vm = this;
            var url = '';
            if (reprocess == undefined || reprocess == 'undefined' || reprocess == 'null')
                url = `batchId=${item.batch_id}`;
            else
                url = `batchId=${item.batch_id}&reProcess=${reprocess}`;

            this.BatchProcessingListing.filter(x => x.batch_id == item.batch_id)[0].status = 'Starting';

            this.getProgress(index, item.batch_id);
            await DataService.CallProcessBatchDataTask(url).then(function (response) {
                if (response.data != "" && response.data != null) {
                    if (response.data.status == 'Success') {
                        if (response.data.result && response.data.result !== '') {
                            vm.requestId = response.data.result.split('=')[1];
                        }
                    }
                }
                else if (response.data.result.status_id == -1) {
                }
            })

        },
         EditBatchProcessData: async function (item, index) {
            var vm = this;
            vm.BatchId= item.batch_id;
            vm.isCreateBatch = true;
        },
        CloseBatchDetail: function () {
            var vueObj = this;
            vueObj.isDetail = false;
        },
        CloseBatchProcess: function() {
            var vueObj = this;
            vueObj.isOpenProcessData = false;
        },
        FetchData: async function () {
            var vueObj = this;
            vueObj.BatchProcessingListing = [];
            var url = `search=${vueObj.searchCondition}&pageSize=${vueObj.pageSize}&pageNum=${vueObj.pageNum}&sortBy=${vueObj.sortBy}&sortExp=${vueObj.sortExp}&batchId=${vueObj.BatchId ?? ''}`;
            const obj = JSON.stringify(url);
            var requestPayload = { objData: obj };
            await DataService.GetBatchCycleListing(requestPayload).then(function (response) {
                if (response.data != "" && response.data != null) {
                    if (response.data.result.data.length > 0) {
                        vueObj.BatchProcessingListing = response.data.result.data.map(item => {
                            item.created_at = new Date(item.created_at);
                            item.progressValue = 0;  // Initialize progressValue for each item
                            return item;
                        });
                        vueObj.TotalRecords = response.data.result.data[0].TOTALRECORDS;
                        vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.pageSize);
                        vueObj.CurrentPage = vueObj.pageNum;
                        vueObj.noRecord = false;
                    } else {
                        vueObj.noRecord = true;
                        vueObj.TotalRecords = 0
                        vueObj.BatchProcessingListing = [];
                    }
                }
                else {
                    vueObj.noRecord = true;
                    vueObj.TotalRecords = 0
                    vueObj.BatchProcessingListing = [];
                }
            })
        },
         leftCommonSearch(value) {
            this.pageNum = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchConditionJson !== 'undefined' && value.searchConditionJson !== null) {
                this.searchCondition = value.searchConditionJson.replace('=', '');
            } else {
                this.searchCondition = "";
            }
            this.FetchData();
        },
        actionButtonClick(callbackfunction, item, index, event) {
            switch (callbackfunction) {
                case 'Process':
                    this.callProcessBatch(item, index);
                    break;
                case 'Detail':
                    this.recordDetail(item);
                    break;
                case 'Reprocess':
                    this.reProcess(item, index);
                    break;
                case 'EditRuleService':
                    this.EditBatchProcessData(item, index);
                    break;
            }
        },

        renderActions: function (row) {
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = { ...actionItem };
                switch (action.title) {
                    
                    case 'Edit':
                        action.isVisible = true;
                         action.isDisabled = row.status == 'Generated' ? false : true;
                        break;
                    case 'Process':
                        action.isVisible = true;
                        action.isDisabled = (row.status == 'Completed' || row.status == 'Started') ? true : false;
                        break;
                    case 'Detail':
                        action.isVisible = true;
                        break;
                    case 'Reprocess':
                        action.isVisible = true;
                        action.isDisabled = (row.status == 'Generated' || row.status == 'Started') ? true : false;
                        break;
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        pagerMethod(value) {
            this.pageSize = value.PageSize;
            this.pageNum = value.PageNumber;
            this.FetchData();
        },
        getStatusClass: function (status) {
            var returnClass = '';
            switch (status) {
                case 'Active':
                    returnClass = 'px-2 text-center bg-success text-white d-block';
                    break;
                case 'Inactive':
                    returnClass = 'px-2 text-center bg-danger text-white d-block';
                    break;
                default:
                    returnClass = '';
            }
            return returnClass;
        },
        commonHeaderButtonClick(event) {
            switch (event.callbackfunction) {
                case "GenerateBatch":
                    this.GenerateBatch();
                    break;
                case "queueList":
                    this.navigateToQueueList();
                    break;

            }
        },
        GenerateBatch: function () {
            var vm = this;
            vm.BatchId= 0;
            vm.isCreateBatch = true;
        },
        sortdata: function (sortBy) {
            this.sortBy = sortBy;
            var sortExp = 'ASC';
            if (this.sortExp == 'ASC') {
                sortExp = 'DESC';
            }
            this.sortExp = sortExp;
            this.sortBy = sortBy;
            this.sortExp = sortExp;
            this.pageNum = 1;
            this.GroupStart = this.pageNum;
            this.GroupEnd = this.pageNum + 2;
            this.FetchData();
        },
        recordDetail(item) {
            var vm = this;
            if (vm.requestId !== null && vm.requestId !== '') {
                window.open(vm.$router.resolve({ name: 'BillLog', params: { requestId: vm.requestId } }).href, '_blank');
            }
            else {
                window.open(vm.$router.resolve({ name: 'BillLog', params: { requestId: item.request_id } }).href, '_blank');
            }
            // this.isRecordDetail = true
        },
        reProcess(item, index) {
            this.isReprocess = true;
            this.reProcessDetail = item;
            this.reProcessIndex = index;
        },
        navigateToQueueList() {
            var vm = this;
            vm.$router.push({ name: 'QueueList' });
        }


    }
})
</script>
